import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-admiou',
  templateUrl: './admiou.component.html',
  styleUrls: ['./admiou.component.css']
})
export class AdmiouComponent implements OnInit {
  ancho:number;
  estado:boolean;
  constructor() {
    this.ancho= 10;
    this.estado = false;
  
   }

  ngOnInit(): void {
   let interval =  setInterval(() => {
      if (this.ancho < 250) {
        this.ancho += 10;
       
      }else{
        clearInterval(interval);
        this.estado=true;
      }
    }, 20);
  }

}
