import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-admproc',
  templateUrl: './admproc.component.html',
  styleUrls: ['./admproc.component.css']
})
export class AdmprocComponent implements OnInit {
  ancho:number;
  estado:boolean;
  constructor() { 
    this.ancho=10;
    this.estado = false;
  }

  ngOnInit(): void {
    let interval = setInterval(() => {
      if (this.ancho < 250) {
        this.ancho += 10;
      }else{
        this.estado = true;
        clearInterval(interval);
      }
    }, 20);
  }
}
