import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-admmemo',
  templateUrl: './admmemo.component.html',
  styleUrls: ['./admmemo.component.css']
})
export class AdmmemoComponent implements OnInit {
  ancho:number;
  estado:boolean;
  constructor() { 
    this.ancho=10;
    this.estado =false;
  }

  ngOnInit(): void {
    let interval = setInterval(() => {
      if (this.ancho < 250) {
        this.ancho += 10;
       
      }else{
        clearInterval(interval);
        this.estado = true;
      }
    }, 20);
  }

}
