import { Component, OnInit } from '@angular/core';
import { AnimationService } from 'src/app/services/animation.service';

@Component({
  selector: 'app-admarc',
  templateUrl: './admarc.component.html',
  styleUrls: ['./admarc.component.css']
})
export class AdmarcComponent implements OnInit {
  inclinacion:number;
  estado:boolean;
  ancho:number;
  npro:string;
  constructor(private animaservice: AnimationService) {
    this.ancho=10;
    this.estado= false;
   }

  ngOnInit(): void {
    let interval = setInterval(() => {
      if (this.ancho < 250) {
        this.ancho += 10;
        this.inclinacion= this.animaservice.getnum1();
        console.log(this.inclinacion);
      }else{
        clearInterval(interval);
        this.estado= true;
      }
    }, 20);
    
    console.log(this.inclinacion);
  }

}
